import React, { memo } from 'react';
import './AvisoSonoro.css';

const AvisoSonoro = ({ sound, toggleSound }) => (
  <button
    className={`AvisoSonoro ${sound && 'active'}`}
    onClick={toggleSound}
    title={sound ? 'Desabilitar aviso sonoro' : 'Habilitar aviso sonoro'}
  >
    <i className={`fa fa-volume-${sound ? 'up' : 'mute'}`} />
  </button>
);

export default memo(AvisoSonoro);
