import React, { useRef, useContext } from 'react';
import { useDrag, useDrop } from 'react-dnd';
import './styles.css';

import TaskContext from '../ListaTarefa/context';

export default function Tarefa({ task, index }) {
  const ref = useRef();
  const { move, handleStatus } = useContext(TaskContext);
  const [{ isDragging }, dragRef] = useDrag({
    item: { type: 'TAREFA', id: task.id, index },
    collect: monitor => ({
      isDragging: monitor.isDragging()
    })
  });

  const [, dropRef] = useDrop({
    accept: 'TAREFA',
    hover(item, monitor) {
      if (item.id === task.id) return;
      const dragged = item;
      const target = task;
      const targetSize = ref.current.getBoundingClientRect();
      const targetCenter = (targetSize.bottom - targetSize.top) / 2;
      const draggedOffset = monitor.getClientOffset();
      const draggedTop = draggedOffset.y - targetSize.top;

      if (dragged.order < target.order && draggedTop < targetCenter) return;
      if (dragged.order > target.order && draggedTop > targetCenter) return;

      move(item.index, index);
      item.index = index;
    }
  });

  dragRef(dropRef(ref));

  return (
    <div ref={ref} className={isDragging ? 'Arrastar tarefa' : 'Tarefa'}>
      <div>{task.title}</div>
      <span onClick={() => handleStatus(task)}>
        {task.closed ? 'Fazer' : 'Finalizar'}
      </span>
    </div>
  );
}
