import React, { memo } from 'react';
import './Controles.css';

const Controls = ({ start, reset, pause, status }) => (
  <div className="Controles">
    {!status && (
      <button onClick={start} className="inicio">
        Iniciar
      </button>
    )}

    {status === 'Finalizado' && (
      <button onClick={start} className="inicio">
        Reiniciar
      </button>
    )}

    {(status === 'Pausado' || status === 'Tempo') && (
      <div>
        <button onClick={reset} className="reiniciar">
          Desligar
        </button>
        <button
          onClick={pause}
          className={status === 'Pausado' ? 'continuar' : 'pausa'}
        >
          {status === 'Pausado' ? 'Continuar' : 'Pausar'}
        </button>
      </div>
    )}
  </div>
);

export default memo(Controls);
