import React, { memo } from 'react';
import './styles.css';

const VerTarefas = ({ tarefa, painelTarefa }) => (
  <button
    className={`PainelTarefas ${tarefa && 'active'}`}
    onClick={painelTarefa}
    title={tarefa ? 'Desabilitar painel de tarefas' : 'Habilitar painel de tarefas'}
  >
    <i className={'fa fa-tasks'} />
  </button>
);

export default memo(VerTarefas);
