import React, { memo, useState, useEffect } from 'react';
import produce from 'immer';
import TaskContext from './context';
import Tarefa from '../Tarefa';
import TipoTempo from '../../TipoTempo';

import './styles.css';

const ListaTarefa = ({ selectedTaskType }) => {
  const [input, setInput] = useState('');
  const taskStatus = [
    { name: 'Todas', value: -1 },
    { name: 'Fazendo', value: false },
    { name: 'Finalizadas', value: true }
  ];

  const [tasks, setTasks] = useState(
    JSON.parse(window.localStorage.getItem('pomodoro-react-tasks')) || []
  );
  const [selectedStatus, setSelectedStatus] = useState(taskStatus[0]);

  useEffect(() => {
    window.localStorage.setItem('pomodoro-react-tasks', JSON.stringify(tasks));
  }, [tasks]);

  function move(from, to) {
    setTasks(
      produce(tasks, draft => {
        const taskMoved = draft[from];
        draft.splice(from, 1);
        draft.splice(to, 0, taskMoved);
      })
    );
  }

  function handleStatus(task) {
    console.log('task:', task);
    setTasks(
      produce(tasks, draft => {
        const foundIndex = draft.findIndex(item => item.id === task.id);
        draft[foundIndex].closed = !draft[foundIndex].closed;
      })
    );
  }

  function addTask() {
    setTasks(
      produce(tasks, draft => {
        draft.push({ id: draft.length + 1, title: input, closed: false });
      })
    );
    setInput('');
  }

  function clearTask() {
    localStorage.removeItem("pomodoro-react-tasks");
    window.location.reload();
  }

  return (
    <TaskContext.Provider value={{ tasks, move, handleStatus }}>
      <TipoTempo
        types={taskStatus}
        selected={selectedStatus}
        changeType={setSelectedStatus}
      />
      <div className="Tarefa">
        <input
          value={input}
          onChange={e => setInput(e.target.value)}
          placeholder="Inserir tarefa"
        />
        <span onClick={addTask}>{'Criar'}</span><span onClick={clearTask}>{'Limpar todas'}</span>
      </div>
      <div className="Tarefas">
        <div className="Tarefas-box">
          {tasks.length > 0 ? (
            tasks
              .filter(
                task =>
                  task.closed === selectedStatus.value ||
                  selectedStatus.value === -1
              )
              .map((task, index) => (
                <Tarefa key={task.id} index={index} task={task} />
              ))
          ) : (
            <div className="Tarefa">Sem tarefas</div>
          )}
        </div>
      </div>
    </TaskContext.Provider>
  );
};

export default memo(ListaTarefa);
